@import '../../styles/sass/variables';
@font-face {
  font-family: "Josefin Sans";
  src: url('../../../assets/fonts/JosefinSans.ttf')
}

.title {
  margin-bottom: 30px;
  position: relative;

  &::before {
    position: absolute;
    content: '';
    background-color: $textColor;
    width: 55px;
    height: 1px;
    bottom: -10px;
    left: 50%;
    transform: translate(-50%, 50%);
  }

  .title h2 {
    color: $textColor;
    margin: 0;
    font-family: $titleFontFamily;
    font-size: 36px;
    font-weight: 400;
    text-transform: uppercase;
    font-style: italic;
    line-height: 1.2em;
    letter-spacing: 3px;
  }

}


